html {
  font-family: 'Montserrat', sans-serif;
}

.container {
  margin: 100px auto;
  width: 1280px;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 46px;
  color: #1E1E1E;
  line-height: 1.2;
  text-align: center;
  margin: 0 auto;
  width: 780px;
  margin-bottom: 30px;
}

h2 {
  color: #1E1E1E;
  font-family: 'Montserrat', sans-serif;
  font-size: 34px;
  line-height: 1.2;
}

h3 {
  color: #1E1E1E;
  font-family: 'Montserrat', sans-serif;
  font-style: italic;
  font-size: 28px;
  line-height: 1.2;
  text-align: center;
}

p {
  font-family: 'Cardo', serif;
  color: #1E1E1E;
  font-size: 14px;
  line-height: 1.4;
  margin-bottom: 30px;
}

a {
  text-decoration: none;
  font-family: 'Cardo', serif;
  color: blue;
  font-size: 14px;
  line-height: 1.4;
}

.italic-subtitle {
  color: #1E1E1E;
  font-family: 'Montserrat', sans-serif;
  font-style: italic;
  font-size: 28px;
  line-height: 1.2;
}

.nav {
  display: flex;
  justify-content: flex-end;
}

.nav_container {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav_item {
  display: block;
  font-family: 'Montserrat', sans-serif;
  font-size: 11px;
  color: #1E1E1E;
  margin: 0 0 0 100px;
  padding: 0;
  text-transform: uppercase;
}

.nav_link {
  text-decoration: none;
  color: #1E1E1E;
}

.add {
  display: flex;
  justify-content: flex-end;
  margin: 130px 0 90px 0;
}

.btn {
  color: #1E1E1E;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 1.3;
  background: white;
  border: 1px solid #9B9B9B;
  border-radius: 50px;
  height: 60px;
  width: 300px;
  text-transform: uppercase;
  cursor: pointer;
}

.info {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.info_item {
  color: #9B9B9B;
  display: block;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 1.3;
  padding: 0;
}

.info_mark {
  color: #1E1E1E;
  font-weight: bold;
}

.info_item:not(:last-child)::after {
  content: '\2022';
  font-weight: bold;
  padding: 0 20px;
}

.action_buttons {
  margin: 40px 0 20px 0;
  display: flex;
  justify-content: flex-end;
}

.action_btn {
  padding: 0;
  background: none;
  border: none;
  color: #1E1E1E;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 1.3;
  cursor: pointer;
}

.content {
  /* display: flex; */
  margin: 80px auto 100px;
  width: 800px;
  height: auto;
  text-align: center;
}

.action_btn:not(:last-child)::after {
  content: '\007c';
  padding: 5px;
}

.article-container {
  text-align: center;
  width: auto;
}

.footer {
  display: flex;
  justify-content: space-between;
  margin: 35px 71px 100px 71px;
}

.footer_link {
  cursor: pointer;
  background: none;
  border: none;
  color: #1e1e1e;
  font-family: 'Montserrat', sans-serif;
  font-size: 34px;
  line-height: 1.2;
  text-decoration: none;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;

  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);

}

.is-modal-open {
  display: block;
}

.modal-content {
  background-color: #fefefe;
  margin: 4% auto;
  padding: 50px 75px;
  border: 1px solid #888;
  width: 1000px;
  height: 750px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.inputs__container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 50px;
}

.input {
  width: 400px;
  height: 35px;
  border: 1px solid #9b9b9b;
  border-radius: 5px;
  color: #9B9B9B;
  font-size: 18px;
  font-family: 'Cardo', serif;
  line-height: 1.4;
  padding-left: 10px;
  margin-bottom: 25px;
}

.input-imgUrl {
  width: 100%;
}

img {
  height: 720px;
}

.img {
  margin-top: 60px;
}

article {
  margin-top: 160px;
}

.textarea {
  display: block;
  border: 1px solid #9b9b9b;
  border-radius: 5px;
  font-size: 18px;
  font-family: 'Cardo', serif;
  width: 100%;
  box-sizing: border-box;
  padding-left: 10px;
  overflow: auto;
}

.modal__buttons {
  /* margin: 130px 0 90px 0; */
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  /* margin-bottom: 50px; */
}

.button--pink {
  background: #FFE3E3;
}